.markets-block {
    margin-top: 72px;

    &__grid {
        margin-top: 73px;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
        justify-content: space-between;
        grid-gap: 45px 40px;

        @include mobile {
            margin-top: 0;
            padding: 0;
        }
    }

    &__slider {
        @include mobile {
            margin-top: 40px;

            .swiper-slide {
                padding: 10px 0;
                width: 120px;
                display: flex;
                justify-content: center;
            }
        }
    }
    
    &__nav {
        display: none;

        @include tablet {
            display: block;
        }
    }

    &__arrow {
        min-width: 50px;
        min-height: 50px;
        border-radius: 50%;
        background-color: #fff;
        box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.075);

        &:after {
            transform: scale(.6);
            color: black;
        }
    }

    &__item {
        position: relative;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background-color: $yellow;
        display: block;

        &:hover {
            .markets-block__icon {
                margin-left: -5px;
            }
        }

        @include mobile {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__percent {
        font-weight: 800;
        font-size: 16px;
        line-height: 19px;
        color: #fff;
        width: 38px;
        height: 38px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $soft-black;
        border-radius: 50%;
        position: absolute;
        top: -5px;
        left: -5px;

        &_red {
            background-color: $red;
        }
    }

    &__icon {
        position: absolute;
        bottom: 50%;
        left: 35%;
        transform: translateY(50%);
        max-width: 180%;
        transition: ease-out .18s;

        @include mobile {
            max-width: 120%;
            position: relative;
            bottom: 0;
            left: 0;
            transform: unset;
        }
    }

    &__more {
        margin: 64px auto 0;
        display: block;
    }
}